  <div class="swap-complete">
    <TextHeader class="swap-complete__header block-border-bottom">
      {{ tp('title') }} {{ isCompletedManually ? '*' : '' }}
    </TextHeader>
    <div class="swap-complete__main">
      <div class="swap-complete__main-info block-border-bottom">
        <ExchangeCoinInfo
          :coin="sendCoin"
          hide-rate
          :label="tp('sentTitle')"
          :sum="4.123"
        />
        <ExchangeCoinInfo
          :coin="receiveCoin"
          hide-rate
          :label="tp('receivedTitle')"
          :sum="0.0929345"
        />
      </div>
      <div class="swap-complete__main-address d-flex flex-column align-items-center block-border-bottom">
        <Text theme="primary" weight="500" size="14px" mb="12px">
          {{ tp('receivingAddress') }}
        </Text>
        <div class="d-flex justify-content-center">
          <Text
            weight="700"
            :size="isMobile ? '17px' : '19px'"
            mr="12px"
