            {{ address }}
          </Text>
          <Icon v-if="!isMobile" name="copy" height="18px" width="16px" opacity="0.4" />
        </div>
      </div>
      <component
        :is="'SwapCompleteShare' + (isMobile ? 'Mobile' : 'Desktop')"
        :reference-url="referenceUrl"
        @copy="copyUrl"
      />
    </div>
  </div>
