<script>
import { MODULES_NAMES } from '@/shared/common/modules'
import { defineAsyncComponent, ref } from 'vue'
import { copy } from '@/shared/utils/copyToClipboard'
import { useI18n } from '@/shared/plugins/i18n'
import { useRoute } from 'vue-router'
import { useBreakpointsMedia } from '@/shared/plugins/breakpoints'
import { useGetters, useMutations } from '@/shared/plugins/vuex'
import Text from '@/shared/components/UIKit/Text/Text'
import Icon from '@/shared/components/Icon/Icon'
import TextHeader from '@/client/components/TextHeader'
import ExchangeCoinInfo from '@/client/views/ExchangeConfirmation/ExchangeCoinInfo'

export default {
  name: 'SwapComplete',
  components: {
    Icon,
    Text,
    TextHeader,
    ExchangeCoinInfo,
    SwapCompleteShareMobile: defineAsyncComponent(() => import('@/client/views/SwapComplete/SwapCompleteShareMobile')),
    SwapCompleteShareDesktop: defineAsyncComponent(() => import('@/client/views/SwapComplete/SwapCompleteShareDesktop'))
  },
  setup () {
    const route = useRoute()
    const { t, tp } = useI18n('swapComplete')
    const { isMobile } = useBreakpointsMedia()
    const {
      getReceiveCoin: receiveCoin,
      getSendCoin: sendCoin
    } = useGetters(MODULES_NAMES.EXCHANGE)
    const {
      SET_TOOLS: setTools
    } = useMutations(MODULES_NAMES.TOOLS)

    const isCompletedManually = ref(!!route.query.manually)

    const address = ref('MQi2aB3HbFoNkntUygxnSyChpXSS3qhgTU')
    const referenceUrl = ref('https://switch.to/c/521dfasf25126hgsdhsh21')

    const copyUrl = () => {
      copy(referenceUrl.value)
    }

    setTools([])

    return {
      t,
      tp,
      address,
      sendCoin,
      isMobile,
      receiveCoin,
      referenceUrl,
      isCompletedManually,
      copyUrl
    }
  }
}
</script>

